import axios from 'axios';
import { API } from '../API';

const getData = (res) => ({
  type: "getData",
  res
});

const getDataToPage = (res, page) => ({
  type: "getDataToPage",
  res,
  page
});

const getDataToPagePrev = (res, page) => ({
  type: "getDataToPagePrev",
  res,
  page
});

const fetchData = (name, set, rarity, types, subtype, supertype, page) => {
  return (dispatch) => {
    dispatch({type: "isFetching"});
    axios.get(`${API}cards`, {
      params: {
        name,
        set,
        rarity,
        types,
        subtype,
        supertype,
        page
      }
    }).then(res => {
          dispatch({type: "doneFetching"});
          dispatch(getData(res));
    }).catch((error) => {
          console.error(error);
          alert("Error getting Data, please try again!");
    });
  };
};

const fetchToPage = (url, page) => {
  return (dispatch) => {
    dispatch({type: "isFetching"});
    axios.get(`${url}`, {
      params: {
        page
      }
    }).then(res => {
          dispatch({type: "doneFetching"});
          dispatch(getDataToPage(res, page));
    }).catch((error) => {
          console.error(error);
          alert("Error getting Data, please try again!");
    });
  };
};

const fetchToPagePrev = (url, page) => {
  return (dispatch) => {
    dispatch({type: "isFetching"});
    axios.get(`${url}`, {
      params: {
        page
      }
    }).then(res => {
          dispatch({type: "doneFetching"});
          dispatch(getDataToPagePrev(res, page));
    }).catch((error) => {
          console.error(error);
          alert("Error getting Data, please try again!");
    });
  };
};

export { fetchData, fetchToPage, fetchToPagePrev };
