import React, { Component } from 'react';
import { connect } from 'react-redux';
import { fetchData } from '../actions';
import axios from 'axios';
import { API } from '../API';
import './SearchBar.css';

class SearchBar extends Component {
  constructor (props) {
    super (props);
    this.state = {
      term: "",
      setList: [{name: ""}],
      set: "",
      rarityList: ["Commmon", "Uncommon", "Rare", "Rare Holo"],
      rarity: "",
      typeList: [""],
      type: "",
      subtypeList: [""],
      subtype: "",
      supertypeList: [""],
      supertype: "",
      isLoading: true
    };
  }
  handleTermChange = (e) => {
    this.setState({
      term: e.target.value
    });
  }
  handleSetChange = (e) => {
    this.setState({
      set: e.target.value
    });
  }
  handleRarityChange = (e) => {
    this.setState({
      rarity: e.target.value
    });
  }
  handleTypeChange = (e) => {
    this.setState({
      type: e.target.value
    });
  }
  handleSubtypeChange = (e) => {
    this.setState({
      subtype: e.target.value
    });
  }
  handleSupertypeChange = (e) => {
    this.setState({
      supertype: e.target.value
    });
  }
  handleSearch = (e) => {
    e.preventDefault();
    this.props.fetchData(
      this.state.term,
      this.state.set,
      this.state.rarity,
      this.state.type,
      this.state.subtype,
      this.state.supertype,
      "1"
    );
  }
  componentDidMount () {
    axios.get(`${API}sets`).then(res => {
          this.setState({
            setList: [...res.data.sets]
          });
    }).catch((error) => {
          console.error(error);
          alert("Error getting Data, please try reloading!");
    });
    axios.get(`${API}types`).then(res => {
          this.setState({
            typeList: [...res.data.types]
          });
    }).catch((error) => {
          console.error(error);
          alert("Error getting Data, please try reloading!");
    });
    axios.get(`${API}subtypes`).then(res => {
          this.setState({
            subtypeList: [...res.data.subtypes]
          });
    }).catch((error) => {
          console.error(error);
          alert("Error getting Data, please try reloading!");
    });
    axios.get(`${API}supertypes`).then(res => {
          this.setState({
            supertypeList: [...res.data.supertypes]
          });
    }).catch((error) => {
          console.error(error);
          alert("Error getting Data, please try reloading!");
    });
  }
  render () {
    const mapItems = (array) => array.map((item) => <option value={item} key={item.replace(/\s/g,"")}>{item}</option>);
    const isLoaded = Boolean(
      this.state.setList.length > 1
      && this.state.typeList.length > 1
      && this.state.subtypeList.length > 1
      && this.state.supertypeList.length > 1
      && !this.props.isFetching
    );
    return (
      <div className="SearchBar">
        <form action="" onSubmit={this.handleSearch}>
          <label htmlFor="name">Name: </label>
          <input
            id="name"
            name="name"
            type="text"
            value={this.state.term}
            onChange={this.handleTermChange}
            placeholder="Card Name..."
          />
          <label htmlFor="setList">Set: </label>
          <input
            id="setList"
            name="setList"
            list="set"
            onChange={this.handleSetChange}
            placeholder="From Card Set..."
          />
          <datalist id="set" name="set">
           {this.state.setList.map((set) => <option value={set.name} key={set.name.replace(/\s/g,"")}>{set.name}</option>)}
          </datalist>
          <label htmlFor="rarity">Rarity: </label>
          <select id="rarity" name="rarity" onChange={this.handleRarityChange}>
            <option value="">All</option>
            {mapItems(this.state.rarityList)}
          </select>
          <label htmlFor="type">Type: </label>
          <select id="type" name="type" onChange={this.handleTypeChange}>
            <option value="">All</option>
            {mapItems(this.state.typeList)}
          </select>
          <label htmlFor="subtype">Subtype: </label>
          <select id="subtype" name="subtype" onChange={this.handleSubtypeChange}>
            <option value="">All</option>
            {mapItems(this.state.subtypeList)}
          </select>
          <label htmlFor="supertype">Supertype: </label>
          <select id="supertype" name="supertype" onChange={this.handleSupertypeChange}>
            <option value="">All</option>
            {mapItems(this.state.supertypeList)}
          </select>
          <input
            type="submit"
            value={!isLoaded ? "LOADING..." : "SEARCH"}
            disabled={
              isLoaded ? false : true
            }
          />
        </form>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  isFetching: state.isFetching
});

const mapDispatchToProps = (dispatch) => ({
  fetchData: (
    term,
    set,
    rarity,
    types,
    subtype,
    supertype,
    page
  ) => dispatch(fetchData(
    term,
    set,
    rarity,
    types,
    subtype,
    supertype,
    page
  ))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SearchBar);
