const reducers = (state, action) => {
  const chunkArray = (arr, size) => {
    let results = [];
    while (arr.length) {
        results.push(arr.splice(0, size));
    }
    return results;
  };
  switch (action.type) {
    case "isFetching":
      return {
        ...state, isFetching: true
      };
    case "doneFetching":
      return {
        ...state, isFetching: false
      };
    case "getData":
      //console.log(action.res.headers["total-count"]);
      return {
        ...state,
        data: [...action.res.data.cards],
        totalPages: Math.round(action.res.headers["total-count"] / 10),
        splitArray: [...chunkArray(action.res.data.cards, 10)],
        lastUrl: action.res.request.responseURL,
        currentPoint: 0,
        currentPagePos: 1
      };
    case "getDataToPage":
      return {
        ...state,
        data: [...action.res.data.cards],
        splitArray: [...chunkArray(action.res.data.cards, 10)],
        lastUrl: action.res.request.responseURL,
        currentPoint: 0,
        currentPagePos: action.page
      };
    case "getDataToPagePrev":
      return {
        ...state,
        data: [...action.res.data.cards],
        splitArray: [...chunkArray(action.res.data.cards, 10)],
        lastUrl: action.res.request.responseURL,
        currentPoint: 9,
        currentPagePos: action.page
      };
    case "next":
      return {
        ...state,
        currentPoint: Number(state.currentPoint) + 1
      }
    case "prev":
      return {
        ...state,
        currentPoint: Number(state.currentPoint) - 1
      }
    default:
      return state;
  }
};

export default reducers;
