import React from 'react';
import { connect } from 'react-redux';
import './Buttons.css';
import { fetchToPage, fetchToPagePrev } from '../actions';

const Buttons = (props) => {
  const [
    list,
    smallPages,
    largePages,
    point,
    pos,
    lastUrl,
    fetch
  ] = [
    props.splitArray,
    props.splitArray.length,
    props.totalPages,
    props.currentPoint,
    props.currentPagePos,
    props.lastUrl,
    props.isFetching
  ];
  const next = () => {
    const newPoint = Number(point) + 1;
    if (newPoint + 1 > smallPages) {
      if ((pos - 1) * 10 + Number(point) + 1 < largePages) {
        props.fetchToPage(lastUrl, Number(pos) + 1);
      } else {
        return;
      }
    }
    else {
      props.next();
    }
  };
  const prev = () => {
    const newPoint = Number(point) - 1;
    if (newPoint < 0) {
      if ((pos - 1) * 10 + Number(point) + 1 > 0) {
        props.fetchToPagePrev(lastUrl, Number(pos) - 1);
      } else {
        return;
      }
    }
    else {
      props.prev();
    }
  };
  const ratio = [(pos - 1) * 10 + Number(point) + 1, props.totalPages];
  return (
      <div className="Buttons">
        {
          list.length > 0 &&
            <div className="ratio">
              {ratio[0]} / {ratio[1]}
            </div>
        }
        {
          list.length > 0 &&
            <button
              onClick={prev}
              disabled={ratio[0] === 1 || fetch ? true : false}
            >
              Previous 10 Cards
            </button>
        }
        {
          list.length > 0 &&
            <button
              onClick={next}
              disabled={ratio[0] === ratio[1] || fetch ? true : false}
            >
              Next 10 Cards
            </button>
        }
      </div>
    );
};

const mapStateToProps = (state) => ({
  isFetching: state.isFetching,
  totalPages: state.totalPages,
  splitArray: state.splitArray,
  lastUrl: state.lastUrl,
  currentPoint: state.currentPoint,
  currentPagePos: state.currentPagePos
});

const mapDispatchToProps = (dispatch) => ({
  fetchToPage: (url, page) => dispatch(fetchToPage(url, page)),
  fetchToPagePrev: (url, page) => dispatch(fetchToPagePrev(url, page)),
  next: () => dispatch({type: "next"}),
  prev: () => dispatch({type: "prev"})
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Buttons);
