import { createStore, applyMiddleware } from 'redux';
import reducers from '../reducers';
import ReduxThunk from 'redux-thunk';

const initialState = {
  isFetching: false,
  data: [],
  totalPages: 0,
  splitArray: [],
  lastUrl: "",
  currentPoint: 0,
  currentPagePos: 0
};

export const store = createStore(reducers, initialState, applyMiddleware(ReduxThunk));
